var app = getApp().globalData
export default {
	data() {
		return {
			finish:false,
			fileList:[], //图片本地地址列表
			imgArr:[], //上传阿里后的列表地址
			nationList:[],
			id:'',
			placeholderName:'请填写',
			placeholderCountry:'请选择',
			placeholderBirth:'请选择',
			birth:'',
			type:'',
			country:'',
			nickname: '',
			idcard: '',
			placeholderIdCard:'请填写',
		    address: '',
			internationalization:'zh',//默认中文
			reasonId:''//重新编辑的id
		}
	},
	onLoad(e) {
		// 获取国籍
		this.getNationality()
		this.reasonId = e.id
		this.getData()
		
	},
	watch: {
	    finish () {
	      if (this.finish) {
				this.imgArr = JSON.stringify(this.imgArr)
				uni.hideLoading();
				this.$api.register_update({
				  token: uni.getStorageSync("token"),
				  user_id: uni.getStorageSync("userId"),
				  // user_id:15000,
				  country_id:this.id,
				  papers_type:this.type,
				  name:this.nickname,
				  idcard:this.idcard,
				  birthday:this.birth,
				  images: this.imgArr,
				  id:this.reasonId
				}).then(res => {
					
				  uni.showToast({
				  	title:res.data.msg,
					icon:'none'
				  })
				  if (res.data.code === '200') {
					  setTimeout(()=>{
						  uni.navigateTo({
						  	url:'../../pages/PersonalIdcard/PersonalIdcard'
						  })
					  }, 500)
				  }
				})
			
	      }
	    }
	},
	methods: {
		getData(){
			
			this.$api.update_detail({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				// user_id:15000,
				id:this.reasonId
				
			}).then(res=>{
				if(res.data.code==='200'){
					if(this.internationalization === 'zh'){
						this.nationList.forEach((item,index)=>{
							if(item.id == res.data.data.country_id){
								this.country= item.name_zh
							}
						})
					}
					if(this.internationalization === 'en'){
						this.nationList.forEach((item,index)=>{
							if(item.id == res.data.data.country_id){
								this.country= item.name_en
							}
						})
					}
					this.id = res.data.data.country_id
					this.birth= res.data.data.birthday.split(' ')[0]
					this.type= res.data.data.papers_type
					this.nickname= res.data.data.name
					this.idcard=res.data.data.idcard
					
					res.data.data.images.forEach((item,index)=>{
						this.fileList.push({url:item.url_image})
					})
					
					
					
				}
			})
		},
		tel(){
			uni.makePhoneCall({
			    phoneNumber: '15645088075' //仅为示例
			});
		},
		// 生日选择
		pickerBirth(e){
			this.birth = e.detail.value
		},
		// 国家选择
		countrySelect(e){
			this.id = this.nationList[e.detail.value].id
			// 英文
			if(this.internationalization==='zh'){
				this.country = this.nationList[e.detail.value].name_zh
				if(this.id == 2 || this.id == 3 || this.id == 4){
					this.type = "当地居民身份证明"
				}else{
					this.type = "护照或海外身份证明"
				}
			}else{
				this.country = this.nationList[e.detail.value].name_en
				if(this.id == 2 || this.id == 3 || this.id == 4){
					this.type = "Local  identification"
				}else{
					this.type = "Passport or overseas identification"
				}
			}
		},
		getNationality(){
			this.$api.User_country_list({
			}).then(res=>{
				this.nationList = res.data.data
			})
		},	
		// 中英文切换
		changeText(){
			this.birth=''
			this.type=''
			this.country=''
			this.nickname= ''
			this.idcard= ''
			this.fileList = []
			if(this.internationalization==='zh'){
				// 这是英文
				this.internationalization = 'en'
				this.placeholderName = 'input'
				this.placeholderCountry = 'choice'
				this.placeholderIdCard = 'input'
				this.placeholderBirth = 'choice'
			}else{
				// 这是汉语
			   this.internationalization = 'zh'
			   this.placeholderName = '请输入姓名'
			   this.placeholderCountry = '请选择国籍'
			   this.placeholderIdCard = '请输入'
			   this.placeholderBirth = '请选择'
			   
			}
			this.getData()
		},
		foreign(){
			uni.navigateTo({
				url:'../../pagesMy/ForeignCard/ForeignCard'
			})
		},
		// // 选择城市
		// pickercity(e){
		// 	this.address = e.detail.value[0].replace(/省|市|自治区|特别行政区|壮族|回族|维吾尔/g, '') + ' ' +e.detail.value[1].replace(/省|市|自治区|特别行政区|壮族|回族|维吾尔|城区/g, '')
		// },
		next () {
		      if (!this.country) {
				  if(this.internationalization==='zh'){
					  uni.showToast({
					      title: '请选择国籍',
					      duration: 1000,
					  	  icon:"none"
					  });
				  }else{
					  uni.showToast({
					      title: 'Please choose your nationality',
					      duration: 1000,
					  	  icon:"none"
					  });
					  
				  }
		      } else if (!this.nickname) {
				  if(this.internationalization==='zh'){
						  uni.showToast({
							  title: '请输入姓名',
							  duration: 1000,
							  icon:"none"
						  });
				  }else{
						  uni.showToast({
							  title: 'Please enter your name',
							  duration: 1000,
							  icon:"none"
						  }); 
				  }
		      } else if (!this.idcard) {
				  if(this.internationalization==='zh'){
						  uni.showToast({
							  title: '请输入证件号码',
							  duration: 1000,
							  icon:"none"
						  });
				  }else{
						  uni.showToast({
							  title: 'Please enter your id number',
							  duration: 1000,
							  icon:"none"
						  }); 
				  }
		      } else if (!this.birth) {
				  if(this.internationalization==='zh'){
						  uni.showToast({
							  title: '请选择出生日期',
							  duration: 1000,
							  icon:"none"
						  });
				  }else{
						  uni.showToast({
							  title: 'Please select date of birth',
							  duration: 1000,
							  icon:"none"
						  }); 
				  }
		      } else if (this.fileList.length===0) {
				  if(this.internationalization==='zh'){
						  uni.showToast({
							  title: '请选择证件照片',
							  duration: 1000,
							  icon:"none"
						  });
				  }else{
						  uni.showToast({
							  title: 'Please select id photo',
							  duration: 1000,
							  icon:"none"
						  }); 
				  }
		      }else {
				  var that = this
				  uni.showLoading({
				      title: '提交中...'
				  });
				  
				  async function put () {
				    for (let i = 0; i < that.fileList.length; i++) { 
							if(that.fileList[i].url.indexOf("aliyuncs.com") != -1){
								// console.log(that.fileList[i])
								that.imgArr.push({
									url_image:that.fileList[i].url
								})
								
								
							}else{
								
								await  uni.uploadFile({
										 url: app.aliUrl,
										 filePath: that.fileList[i].url,
										 name: 'file',
										 formData: {
										   key:"wx/"+"${filename}",
										   signature: app.signature,
										   policy:app.policy,
										   OSSAccessKeyId:app.OSSAccessKeyId,
										   success_action_status:'201'
										 }
								 }).then(res=>{
									 // console.log(res)
									 that.imgArr.push({
										 url_image:res[1].data.split('<Location>')[1].split("</Location>")[0]
									 })
									 
								 })
								
							}
							
							if(that.fileList.length === that.imgArr.length){
								that.finish = true
							}
				  						  
				       }
				  }
				   put()
		      }
		},
		// 删除图片
		del(e){
			this.fileList = this.fileList.filter((item,index)=> index!=e.detail.index)
		},
		// 图片上传后的预览
		afterRead(event) {
		      const { file } = event.detail;
		      // 当设置 mutiple 为 true 时, file 为数组格式，否则为对象格式
		      this.fileList.push({url:file[0].path})
			  setTimeout(function() {
				  uni.pageScrollTo({
				  	scrollTop:1000,
					duration:0
				  })
			  }, 300);
		}
		
		
	}
}
